import React,{ Component } from 'react';
import '../App.css';
import {Animated} from "react-animated-css";

const Form = props => (

			<Animated animationIn="fadeIn" animationOut="fadeOut" isVisible={ props.isVisible }>
			<div id="Title">
				<form className = "Title__form" onSubmit = { props.getWeather }>
				<h1 className = "Title__form-text"> </h1>
					<input className = "Title__form-input" type = "text" 
						placeholder = " " name = "city"/>
					<button className = "Tittle__form-btn">!</button>
					{ props.error && <p className={"Title__form-error " + props.animate}> : {props.error} </p> }
				</form>
			</div>
			</Animated>
);
export default Form;


