import React,{ Component } from 'react';
import {Animated} from "react-animated-css";

const GetWeather = props => (
	<Animated animationIn="fadeIn" animationOut="fadeOut" isVisible={ !props.isVisible }>
			<div className = "weather clouds">
				{ props.city && <p> : {props.city} </p> }
				{ props.country && <p>: {props.country} </p> }
				{ props.temp && <p>: {props.temp} <sup>o</sup> </p> }
				{ props.humidity && <p> : {props.humidity} % </p> }
				{ props.desc && <p> : {props.desc} </p> }
			</div>
	</Animated>
);
export default GetWeather;


